import React, { useEffect, useState } from 'react';
import './Home.css';
import '../Componentes/Header.css'
import wall from './Assets/wall.png';
import star from './Assets/2.png';
import poly from './Assets/Poly.svg';
// import Pagination from '../Componentes/Pagination';
import { useNavigate } from 'react-router';
import Header from '../Componentes/Header';
import axios from 'axios';
import Cards from '../Componentes/Cards';
import CardHead from '../Componentes/CardHead';
import ReactPaginate from 'react-paginate';
import { Spin } from 'antd';

const Home = () => {
  const navigate = useNavigate()
  const token = localStorage.getItem('token')
  useEffect(() => {
    if (!token) {
      return navigate('/')
    }
  }, [])
  const [data, setData] = useState([])
  const [getDats, setGetData] = useState([])
  const [moviesData, setMoviesData] = useState([]);
  const [moviesDataHead, setMoviesDataHead] = useState([]);
  const [loading ,setLoading] =useState([false])
  console.log(getDats);
  let total_pages = data?.total_pages
  const [currentPage, setCurrentPage] = useState(1)
  const [currentPageHead, setCurrentPageHead] = useState(1)
  console.log(currentPageHead);
  useEffect(() => {
    
    const fechMovie = async () => {
    setLoading(true)

      const res = await axios.get(`https://api.themoviedb.org/3/movie/popular?api_key=ae486947dace2da155505d781142e114&language=en-US&page=${currentPage}`)
      setMoviesData(res.data.results);
      setData(res.data)
      console.log(res.data);
      setLoading(false)
    }
    fechMovie()
    // setLoading(true)
  }, [currentPage])
  function handlePageClick({ selected: selectedPage }) {
    setCurrentPage(selectedPage+1);
  }
  function handlePageClickHead({ selected: selectedPage }) {
    setCurrentPageHead(selectedPage+1);
  }
  return (
    <>
      <header>
        <Header setMoviesData={setMoviesData}    setData={setData} currentPage={currentPageHead} setMoviesDataHead={setMoviesDataHead} setGetData={setGetData}/>
      </header>
      <section>
        {loading? <Spin tip="Loading" size="large">
        <div className="content" />
      </Spin>:''}
        <div >
          <img className='home_wall' src={wall} alt='no img' />
        </div>
     {moviesDataHead && moviesDataHead.length ?<h1>Searching</h1>:<h1>Trending</h1>}   
        {/* movies cards */}

        {moviesDataHead && moviesDataHead.length ? <>        <article>
          <div className='cards_layout'>
            <CardHead currentPosts={moviesDataHead} star={star} poly={poly} />
          </div>
        </article>
          <footer className='pagiFoot'>
            <ReactPaginate
              activeClassName={'item active '}
              breakClassName={'item break-me '}
              breakLabel={'...'}
              containerClassName={'pagination'}
              disabledClassName={'disabled-page'}
              marginPagesDisplayed={2}
              nextClassName={"item next "}
              nextLabel={<span style={{ fontSize: '15px' }}>next</span>}
              onPageChange={handlePageClickHead}
              pageCount={getDats.total_pages}
              pageClassName={'item pagination-page '}
              pageRangeDisplayed={2}
              previousClassName={"item previous"}
              previousLabel={<span style={{ fontSize: '15px' }}>pre</span>}
            />
          </footer></> : <>        <article>
            <div className='cards_layout'>
              <Cards currentPosts={moviesData} star={star} poly={poly} />
            </div>
          </article>
          <footer>
            <ReactPaginate
              activeClassName={'item active '}
              breakClassName={'item break-me '}
              breakLabel={'...'}
              containerClassName={'pagination'}
              disabledClassName={'disabled-page'}
              marginPagesDisplayed={2}
              nextClassName={"item next "}
              nextLabel={<span style={{ fontSize: '15px' }}>next</span>}
              onPageChange={handlePageClick}
              pageCount={total_pages}
              pageClassName={'item pagination-page '}
              pageRangeDisplayed={2}
              previousClassName={"item previous"}
              previousLabel={<span style={{ fontSize: '15px' }}>pre</span>}
            />
          </footer></>}


      </section>
    </>
  )
}

export default Home