import axios from 'axios';
import React, { useEffect, useState } from 'react'
import Logo from '../Pages/Assets/properlogo.png';
import { ReactComponent as Vector } from '../Pages/Assets/Vector.svg';
import './Header.css';
import { useNavigate} from 'react-router-dom';
const Header = ({ currentPage, setMoviesData,setGetData,setMoviesDataHead }) => {
    const [search, setSearch] = useState('');
    const navigate = useNavigate()
    const handleChange = (e) => {
        setSearch(e.target.value)
    }
   
    const fechSearch = async () => {
        const res = await axios.get(`https://api.themoviedb.org/3/search/movie?api_key=ae486947dace2da155505d781142e114&language=en-US&query=${search}&page=${currentPage}`)

        if (search.trim().length === 0) {
            // alert('hi')
            const fechMovie = async () => {
                const res = await axios.get(`https://api.themoviedb.org/3/movie/popular?api_key=ae486947dace2da155505d781142e114&language=en-US&page=1`)
                setMoviesData(res.data.results);
                console.log(res.data);
              }
              fechMovie()
        }
        else if (search) {
            setMoviesDataHead(res.data.results);
            setGetData(res.data)
        }
    }
    useEffect(() => {
        const timer=setTimeout(() => {
            fechSearch()
        }, 1000);
       return ()=>{clearTimeout(timer)}
    }, [search,currentPage])
    const handleLogOut=()=>{
        localStorage.removeItem('token')
        navigate('/')
    }
 
    return (
        <>
            <nav>
                <img src={Logo} className='logo' alt='no img' />
                <div className='search_box'>
                    <input placeholder='search hear' onChange={handleChange} />
                    <span className='icon_search'><Vector className='vector' /></span>
                </div>
                <ol>
                    <li> <button onClick={handleLogOut}>logout</button>  </li>
                </ol>
            </nav>
        </>
    )
}

export default Header