import React from 'react';
import { useNavigate } from 'react-router';
import ReactStars from "react-rating-stars-component";
import './Cards.css'
const CardHead = ({ currentPosts, star, poly }) => {
  const navigate = useNavigate()
  const handleDetails = (id) => {
    return navigate(`/home/details/${id}`)
  }
  return (
    <>
      <div className='card_wrapper'>
        {currentPosts && currentPosts?.slice(0, 12).map((items) => {
          return <div key={items.id} className='cards' onClick={() => handleDetails(items.id)}>
            <div className='card_img_div'>
              <img src={`https://image.tmdb.org/t/p/w342${items.poster_path}`} alt='no img' className='cards_img' />
            </div>
            <div className='card-container'>
              <h4 className='card-header'>{items.title}</h4>
              <div className='card_rating'>
                <div className='card_rating-fix'>
                  <ReactStars color="#ffd700" count={Math.round(items.vote_average / 2)} />
                  <span>{items.vote_average / 2}/5</span>
                </div>
                <div className='card-play'>
                  <img src={poly} alt='no img' />
                </div>
              </div>
            </div>
          </div>
        })}

      </div>
    </>
  )
}

export default CardHead